<template>
  <c-menu title="">
    <c-menu-item path="/sys-manage/user" title="" v-if="auth.sys_manage_user"></c-menu-item>
    <c-menu-item path="/sys-manage/role" title="" v-if="auth.sys_manage_role"></c-menu-item>
    <c-menu-item path="/sys-manage/resource" title="" v-if="auth.sys_manage_resource"></c-menu-item>
    <c-menu-sub path="sub" title="">
      <c-menu-item path="/sys-manage/second" title=""></c-menu-item>
      <c-menu-third path="sec" title="">
        <c-menu-item path="/sys-manage/third" title=""></c-menu-item>
      </c-menu-third>
    </c-menu-sub>
  </c-menu>
</template>

<script>
  export default {
    data () {
      return {
        auth: {}
      }
    },
    mounted () {
      this.getPageAuth('sys_manage_user,sys_manage_role,sys_manage_resource').then(res => { this.auth = res })
    }
  }
</script>

<style>
</style>



